/*
	
	
	Stylesheet for Offsite
	
	
*/

.test-form {
	padding-top: 50vh;
	margin-left: 20vw;
}


@import 'fonts.css';
@import 'foundation.min.css';

@media (max-width: 1024px) {
	html {
		font-size: 80%;
	}
}

@media (max-width: 640px) {
	html {
		font-size: 75%;
	}
}


/*
	
	Variables
	
*/

// Colors

$grey: #464447;
$grey-hover: #5b575d;
$brown: #b59b8f;
$offWhite: #f2f2f2;
$beige: #ecece4;

/*
	
	Utlities
	
*/

.bg-image {
	background-size: cover;
	background-position: center center;
	background-repeat: no-repeat;
	width: 100%;
	min-height: 300px;
}

@media (min-width: 0px) {
	.small-text-center {
		text-align: center;
	}
	.small-text-left {
		text-align: left;
	}
	.small-text-right {
		text-align: right;
	}
}

@media (min-width: 640px) {
	.med-text-center {
		text-align: center;
	}
	.med-text-left {
		text-align: left;
	}
	.med-text-right {
		text-align: right;
	}
}

@media (min-width: 1024px) {
	.lg-text-center {
		text-align: center;
	}
	.lg-text-left {
		text-align: left;
	}
	.lg-text-right {
		text-align: right;
	}
}

@media (min-width: 0px) {
	.small-align-center {
		-ms-flex-pack: center;
		justify-content: center;
	}
}

@media (min-width: 1024px) {
	.lg-align-justify {
		-ms-flex-pack: justify;
		justify-content: space-between;
	}
}

/*
	
	Typography
	
	--
	Header One - Top level header for banners and big sections.
	
	--
	Header Two - Header for less smaller sections.
	
	--
	Header Three - Header for smaller paragraphs and subheader underneath header two

	
*/


// Colors
h1, h2, h3, h4, p {
	color: $grey;
	
	&.black {
		color: #000;
	}
	.black {
		color: #000;
	}
	&.white {
		color: $offWhite;
	}
	.white {
		color: $offWhite;
	}
	
	&.book {
		font-weight: 400;
	}
	.book {
		font-weight: 400;
	}

	&.bold {
		font-weight: 700;
	}
	
	&.uppercase {
		text-transform: uppercase;
	}
}

.bold {
	font-weight: 700;
}


// Header One
h1 {
	font-family: "proxima-nova", 'Garamond', serif;
	font-size: 3rem;
	font-weight: 700;
	
	line-height: 1.12em;
	letter-spacing: .284em;
	text-transform: uppercase;
	
	&.white {
		color: #fff;
	}
}

// Header Two
h2 {
	font-family: "gotham", 'Helvetica', 'Verdana', 'Arial', sans-serif;
	font-size: 1.5rem;
	font-weight: 700;
	
	line-height: 1.16em;
	letter-spacing: .137em;
	text-transform: uppercase;
	
	&.book {
		font-weight: 400;
	}
}

// Header Three
h3 {
	font-family: "gotham", 'Helvetica', 'Verdana', 'Arial', sans-serif;
	font-size: 1.125rem;
	font-weight: 700;
	
	line-height: 1.1em;
	letter-spacing: .137em;
	text-transform: uppercase;
	
	&.book {
		font-weight: 400;
	}
	&.off-white {
		color: $offWhite;
	}
}

h4 {
	font-family: "gotham", "Helvetica", "Verdana", "Arial", sans-serif;
	font-size: 1.1rem;
	font-weight: 700;
	
	line-height: 1.1em;
	letter-spacing: .136em;
	text-transform: uppercase;
}

// Paragraph
p {
	font-family: "gotham", serif;
	font-size: .87rem;
	font-weight: 400;
	
	line-height: 2.2em;
	letter-spacing: .137em;
}

// Helper Classes

.lowercase {
	text-transform: none;
}


/*
	
	Interactive
	
	--
	Button - Use as a class on a tags, filled in
	
	--
	Link - Use as a class on a tags or submissions on forms, underlined
	
	--
	Input[type='radio'] - This is a radio tag, must be set up like this:
	
	<input type="radio" id="id-example" name="name-example" />
	<label for="id-example" class="radio">Lorem Ipsum</label>
	
*/

//

a {
	@extend p;
	color: #fff;
	text-transform: uppercase;
	font-weight: 500;
	
	&:hover {
		color: #fdfdfd;
	}
}

// Button
.button,
input[type="submit"] {
	position: relative;
	padding: 10px 25px;
	background: $grey;
	color: #fff;
	border: 1px solid transparent;
	
	transition: all .2s ease-out;
	transform: translateY(0px);
	
	outline: none;
	
	@extend p;
	text-transform: uppercase;
	
	&:hover {
		background: $grey-hover;
		color: #fff;
	}
	&:focus {
		color: #fff;
		outline: none;
	}
	&:active {
		transform: translateY(10px);
	}
	
	// Colors
	&.brown {
		background: $brown;
	}
	&.white {
		background: $offWhite;
		color: $grey;
	}
}

// Link
.link {
	position: relative;
	color: $grey;
	background: transparent;
	padding: 12px 3px;
	
	@extend p;
	text-transform: uppercase;
	
	transition: all .2s ease-out;
	
	&:after {
		display: block;
		content: "";
		position: absolute;
			bottom: 0;
			left: 0;
		width: 100%; height: 1px;
		background: $grey;
		
		transition: all .2s ease-out;
	}
	&:hover {
		color: $grey-hover;
		
		&:after {
			background: #5b575d;
			bottom: -4px;
		}
	}
}

// Input

input[type],
textarea {
	border-radius: 0px;
	box-shadow: none;
	border: none;
	outline: none;
	
	&:hover {
		box-shadow: none;
		outline: none;
	}
	&:focus {
		box-shadow: none;
		outline: none;
	}
}

// Text

input[type="text"] {
	padding-left: 15px;
	&::placeholder {
		@extend p;
		text-transform: uppercase;
	}
}

// textarea

textarea {
	border: 1px solid $grey;
	@extend p;
	text-transform: uppercase;
	padding-left: 15px;
}

// Radio
label.radio {
	border: 1px solid $grey;
	background: transparent;
	color: $grey;
	padding: 15px 25px;
	
	@extend p;
	text-transform: uppercase;
	line-height: 1em;
	
	transition: all .2s ease-out;
	
	&:hover {
		background: #eee;
	}
	
	input[type="radio"]:checked+& {
		background: $brown;
		border: 1px solid $brown;
		color: #fff;
	}
}
input[type="radio"] {
	display: none;
}

/*
	
	General
	
*/

.lines-before-after {
	position: relative;
	
	&:after,
	&:before {
		content: '';
		display: block;
		position: absolute;
			left: 0;
			top: 50%;
		transform: translateY(-50%);
		height: 1px;
		width: 200px;
		background: $brown;
	}
	&:before {
		left: auto;
		right: 0;
	}
}

@media (max-width: 640px) {
	.lines-before-after {
		&:before,
		&:after {
			display: none;
		}
	}
}

// -------- Foundation -------- //

.grid-container {
	max-width: 90rem;
	padding-right: 5vw;
	padding-left: 5vw;
	margin: 0 auto;
}


// -------- Section -------- //
section {
	padding: 100px 0px;
	
	// Section Colors
	&.grey {
		background: $grey;
	}
	&.off-white {
		background: $offWhite;
	}
	&.brown {
		background: $brown;
	}
	&.beige {
		background: $beige;
	}
}

@media (max-width: 640px) {
	section {
		padding: 60px 0px;
	}
}


// -------- Lists --------- //

ul {
	list-style: none;
	padding: 0;
	margin: 0;
	
	li {
		&:before {
			content: "-";
			padding-right: 12px;
		}
	}
	@extend p;
}


// -------- Header -------- //

header {
	position: absolute;
	width: 100%;
	padding: 20px;
	z-index: 400;
	.header-content {
		position: absolute;
			top: 0;
			left: 0;
		background: transparent;
	}
	.logo {
		max-width: 200px;
		img {
			width: 100%;
		}
	}
	nav {
		a {
			margin-left: 80px;
		}
	}
	&.white {
		nav {
			a {
				color: $grey;
			}
			.button {
				color: #fff;
			}
		}
	}
	.hamburger {
		width: 30px;
		display: inline-block;
		cursor: pointer;
	}
}

menu {
	background: $grey;
	position: fixed;
	z-index: 300;
		top: 0;
		left: 0;
	width: 100%;
	height: 100%;
	margin: 0px;
	padding: 15px;
	
	transform: translateY(-100%);
	transition: all .3s ease-out;
	
	.grid-x {
		height: 100%;
	}	
	&.is-open {
		transform: translateY(0%);
	}
	.links {
		margin-bottom: 20px;
	}
}

@media (max-width: 640px) {
	header {
		padding: 10px;
		.grid-container {
			padding: 0px;
		}
	}
}


// -------- Footer -------- //

footer {
	background: $grey;
	padding: 30px;
	
	.logo {
		width: 200px;
		margin-bottom: 20px;
	}
	.greyed-out {
		color: #666;
		font-weight: 400;
	}
	.padded {
		margin-bottom: 20px;
	}
}


// -------- Banner -------- //

.banner {
	@extend section;
	background-size: cover;
	background-position: center center;
	background-repeat: no-repeat;

	
	.banner-content {
		padding-top: 20%;
		padding-bottom: 8vh;
		padding-left:0px;
		width: 40vw; max-width: 850px;
		text-align: left;
		
		h1 {
			margin-bottom: 40px;
		}
	}
	&.inner {
		.banner-content {
			padding-top: 20%;
		}
	}
}

@media (max-width: 1024px) {
	.banner {
		.banner-content {
			width: 60vw;
		}
	}
}

@media (max-width: 640px) {
	.banner {
		.banner-content {
			width: 90vw;
		}
	}
}

/*
	
	Pages
	
*/

// -------- Homepage -------- //

.homepage-options {
	img {
		width: 70px;
	}
	.headline {
		padding-bottom: 50px;
	}
	.item {
		padding: 40px 30px;
		border: 1px solid #000;
		
		h2 {
			padding-top: 30px;
			padding-bottom: 10px;
		}
	}
}

#map {
	height: 450px;
}

.homepage-info {
	h2 {
		padding-bottom: 30px;
	}
}

.homepage-map {
	h1 {
		padding-bottom: 30px;
	}
	img {
		max-width: 900px;
		display: inline-block;
	}
}

.homepage-whats-included {
	p {
		padding: 20px 0px;
	}
}

@media (max-width: 640px) {
	.homepage-info {
		.bg-image {
			margin-bottom: 40px;
		}
	}
} 

// --------- Whats Included -------- //

.whats-included-features {
	
	.header {
		margin-bottom: 70px;
	}
	.items {
		.cell {
			margin-right: 50px;
			margin-bottom: 20px;
		}
	}
}

.whats-included-questions {
	p {
		margin-top: 20px;
	}
}

// -------- FAQ ---------- //

.banner.faq {
	.banner-content {
		margin: 0 auto;
	}
}
.faqs {
	h1 {
		margin-bottom: 70px;
	}
}

.dropdown {
	
	.dropdown-header {
		margin-bottom: 20px;
		.arrow {
			transform: rotate(-90deg);
			transition: all .2s ease-out;
		}
	}
	.dropdown-content {
		max-height: 0px;
		overflow: hidden;
		transition: height .2s ease-out;
	}
	
	&.is-open {
		.dropdown-header {
			.arrow {
				transform: rotate(0deg);
			}
		}
		.dropdown-content {
			max-height: 10000px;
		}
	}
	
}

.topics {
	.category {
		p {
			margin-bottom: 0px;
		}
	}
}


// -------- Contact --------- //

.contact {
	min-height: 100vh;
	padding-top: 15%;
	
	input {
		border: 1px solid $grey;
	}
	h1 {
		margin-bottom: 70px;
	}
}
.contact-info {
	h3 {
		margin-top: 20px;
	}
}

@media (max-width: 640px) {
	.contact-info {
		margin-top: 50px;
	}
}


// -------- Booking -------- //

.booking-container {
	min-height: calc(100vh - 155px);
}
.booking-header {
	margin-top: 70px;
}
.booking-nav {
	margin-top: 30px;
	margin-bottom: 80px;
	
	h3 {
		opacity: .5;
		&:hover {
			opacity: .75;
		}
		&.is-active {
			opacity: 1;
		}
	}
}

.booking-choices {
	.icon {
		margin-bottom: 30px;
	}
	.grid-y {
		height: 100%;
	}
	.inputs {
		h4 {
			margin-top: 30px;
		}
		input {
			margin-top: 10px;
			border: 1px solid #aaa;
			&:hover {
				border: 1px solid #000;
			}
			&:focus {
				border: 1px solid #000;
			}
		}
	}
	.item {
		padding: 30px;
		max-width: 300px;
		border: 1px solid #e5e5e5;
		cursor: pointer;
		
		transition: all .3s ease-out;
		
		h3.bold {
			margin-bottom: 0px;
		}
		h3 {
			margin-bottom: 20px;
		}
		
		&:hover {
			border: 1px solid #666;
		}
		.icon.white {
			display: none;
		}
		.icon.black {
			display: inline-block;
		}
		
		&.is-active {
			padding: 20px;
			background: $grey;
			border: 1px solid $beige;
			box-shadow: 0px 0px 0px 10px $grey;
			margin-top: 10px;
			
			h3 {
				color: $beige;
			}
			p {
				color: $beige;
			}
			h3.bold {
				margin-bottom: 30px;
			}
			.icon.white {
				display: inline-block;
			}
			.icon.black {
				display: none;
			}
		}
	}
	
	&.add-ons {
		.item {
			&.is-active {
				margin-top: 0px;
				padding: 30px;
				box-shadow: none;
			}
		}
	}
}

#calendar,
#day {
	h2 {
		font-size: 1.2rem;
		font-weight: 400;
	}
	th, 
	td, 
	thead, 
	tbody, 
	.fc-divider, 
	.fc-row, 
	.fc-content, 
	.fc-popover, 
	.fc-list-view, 
	.fc-list-heading td {
		border-color: #000;
	}
	.fc-event-container {
		background: $brown;
	}
	.fc-event {
		background: $brown;
		border: none;
	}
	.fc-button {
		background: none;
		box-shadow: none;
		text-shadow: none;
		border: none;
		height: auto;
	}
	.fc-day-number {
		@extend p;
	}
	.fc-bgevent {
		opacity: 1;
		background: $beige;
	}
	.fc-content-skeleton {
		thead {
			height: 0px;
		}
	}
	.fc-title {
		@extend p;
		display: none;
		text-transform: none;
		color: #fff;
	}
	.fc-today {
		background: #fff;
	}
}

.booking-submission {
	margin-top: 80px;

	p.light-grey {
		margin-top: 20px;
		color: #999;
	}
	.button.light-grey {
		background: #f2f2f2;
		color: #464447;
		font-weight: 700;
	}
}
#day {
	th, 
	td, 
	thead, 
	tbody, 
	.fc-divider, 
	.fc-row, 
	.fc-content, 
	.fc-popover, 
	.fc-list-view, 
	.fc-list-heading td {
		border-color: #B3B3B3;
	}
	.fc-list-item-time {
		@extend p;
		text-align: center;
		background: $brown;
		color: #fff;
		font-weight: 700;
	}
	.fc-list-item-title {
		display: none;
	}
	.fc-list-heading {
		display: none;
	}
	.fc-list-item-marker {
		display: none;
	}
}